.partners {
  background: #eeeeee;
  text-align: center;
  box-shadow: inset 0 5px 10px rgba(0, 0, 0, 0.5); }
  .partners__title {
    margin: 20px 0; }
  .partners__container {
    padding-top: 20px;
    padding-bottom: 40px; }

.partner {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 30px; }
  .partner__link {
    display: inline-block;
    max-width: 100%; }
  .partner__img {
    max-width: 100%; }

.bestworks {
  box-shadow: inset 0 5px 10px rgba(0, 0, 0, 0.5);
  background: fixed url("../../../../../sources/client/bg.jpg");
  text-align: center;
  padding-top: 5px; }
  .bestworks__title {
    margin: 40px 0; }
  .bestworks__linkContainer {
    text-align: right;
    padding-bottom: 40px; }

.work {
  width: 100%;
  padding-bottom: 66%;
  overflow: hidden;
  position: relative;
  margin-bottom: 20px; }
  .work:hover .work__img {
    transform: scale(1.3); }
  .work:hover .work__description-container {
    height: 100%; }
  .work__img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    transition: all 1s ease-out; }
  .work__description-container {
    transition: all 0.2s ease-out;
    overflow: hidden;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 0;
    background: rgba(0, 0, 0, 0.5);
    display: flex;
    align-items: center;
    justify-content: center;
    margin: auto; }
  .work__description {
    display: block;
    color: #fff;
    font-size: 24px; }

.make-order__container {
  height: 100%; }

.make-order__wrapper {
  background: linear-gradient(to left, #fff, rgba(255, 255, 255, 0) 30%), url("../../../../../sources/client/make-order2.jpg");
  background-size: cover; }

.make-order__form-container {
  margin-top: 40px;
  margin-bottom: 40px;
  border: 2px solid #018339;
  padding: 40px;
  background: rgba(255, 255, 255, 0.8);
  box-shadow: 0 10px 10px rgba(0, 0, 0, 0.5); }

.make-order__title {
  text-transform: uppercase;
  font-size: 28px;
  padding: 20px;
  color: #018339;
  font-weight: bold;
  text-align: center; }

.make-order__description {
  color: #018339;
  padding: 10px 0 30px 10px;
  font-size: 20px;
  text-align: center; }

.make-order__input {
  width: 100%;
  padding: 20px 10px 10px 10px;
  border: none;
  background: none;
  border-bottom: 1px solid #018339;
  outline: none; }
  .make-order__input::placeholder {
    color: #018339; }

.make-order__btn {
  margin-top: 20px; }

.about-us {
  background: #444;
  padding-top: 30px;
  padding-bottom: 30px; }
  .about-us__title {
    text-transform: none;
    color: #fff;
    padding-top: 20px; }
  .about-us__statistic-container {
    display: flex;
    justify-content: center; }
  .about-us__statistic {
    text-align: right;
    width: 200px;
    position: relative; }
    .about-us__statistic:before {
      content: '';
      width: 200px;
      height: 200px;
      border-radius: 50%;
      background: #018339;
      position: absolute;
      top: 0;
      left: 30px; }
  .about-us h1 {
    font-size: 180px;
    line-height: 120px;
    color: #efefef;
    font-weight: 900;
    position: relative; }
  .about-us span {
    font-size: 50px;
    display: block;
    line-height: 50px; }
  .about-us__description {
    padding-top: 30px;
    color: #fff;
    font-size: 20px;
    line-height: 28px; }
    .about-us__description p {
      margin: 0;
      padding: 0; }
  .about-us__btn {
    margin-top: 30px;
    text-align: right; }

.exhibitions {
  margin-top: 30px;
  height: 150px;
  position: relative; }
  .exhibitions__container {
    background: #018339;
    padding: 30px; }
  .exhibitions__title {
    color: #fff;
    font-size: 24px;
    font-weight: bold; }
  .exhibitions__slider {
    padding: 30px; }

.exhibition__logo {
  width: 100px;
  height: 100px;
  margin: 0 auto;
  border-radius: 50%;
  background: #fff; }

.exhibition__title {
  color: #fff;
  font-size: 16px;
  text-align: center;
  margin: 10px auto; }

.news {
  padding-top: 40px;
  background: url("../../../../../sources/client/news-bg.jpg") center center;
  background-size: cover;
  padding-bottom: 30px; }
  .news__title {
    margin-bottom: 15px; }

.new-item__date {
  font-weight: 900;
  color: #018339;
  font-size: 30px; }

.new-item__title {
  padding: 5px 5px 5px 30px;
  font-size: 18px; }

.new-item__link {
  text-align: right;
  margin-top: 5px; }
